<template>
  <section>
    <div v-if="isLoading">
      <base-spinner></base-spinner>
    </div>
    <base-modal :show="!!error" color="is-danger" title="Leasing Error" @close="handleError">{{ error }}</base-modal>
    <base-modal :show="success" color="is-success" title="Leasing Contract Established" @close="handleSuccess">
      <p>
        A leasing contract for <span class="has-text-weight-semibold">{{ aircraftString }}</span> has been successfully
        created. The aircraft is reserved and will be leased out in FSEconomy as soon as payment of the leasing fee has
        been registered.
      </p>
      <p class="mt-4">
        <button class="button is-success" @click="handleSuccess">OK</button>
      </p>
    </base-modal>
    <base-modal v-if="!isLoading && !error && !success" :show="show" color="is-primary" title="Lease Aircraft"
                @close="$emit('close')">
      <form @submit.prevent="processLeasingForm">
        <div class="field is-horizontal">
          <div class="field-label is-normal">
            <label class="label" for="aircraft">Aircraft</label>
          </div>
          <div class="field-body">
            <div class="field">
              <div class="control is-expanded has-icons-left">
                <input class="input" id="aircraft" type="text" readonly :value="aircraftString">
                <span class="icon is-small is-left">
                <svg-icon type="mdi" :path="aircraftIcon">&nbsp;</svg-icon>
              </span>
              </div>
            </div>
          </div>
        </div>
        <div class="field is-horizontal">
          <div class="field-label is-normal">
            <label class="label" for="price">Rate</label>
          </div>
          <div class="field-body">
            <div class="field">
              <div class="control is-expanded has-icons-left">
                <input class="input" id="price" type="text" readonly :value="rateString">
                <span class="icon is-small is-left">
                <svg-icon type="mdi" :path="rateIcon">&nbsp;</svg-icon>
              </span>
              </div>
            </div>
          </div>
        </div>
        <div class="field is-horizontal">
          <div class="field-label is-normal">
            <label class="label" for="start">Start / End</label>
          </div>
          <div class="field-body">
            <div class="field">
              <div class="control is-expanded has-icons-left">
                <input class="input" id="start" type="text" readonly :value="startDate">
                <span class="icon is-small is-left">
                <svg-icon type="mdi" :path="startIcon">&nbsp;</svg-icon>
              </span>
              </div>
            </div>
            <div class="field">
              <div class="control is-expanded has-icons-left">
                <label for="end"></label><input class="input" id="end" type="text" readonly :value="endDate">
                <span class="icon is-small is-left">
                <svg-icon type="mdi" :path="endIcon">&nbsp;</svg-icon>
              </span>
              </div>
            </div>
          </div>
        </div>
        <div class="field is-horizontal">
          <div class="field-label">
            <!-- Left empty for spacing -->
          </div>
          <div class="field-body">
            <div class="field">
              <p class="help has-text-grey-dark">
                <span class="has-text-weight-semibold">Please note:</span> the indicated dates were calculated based on
                your computer&apos;s system time and are for information only. The actual contract dates will be
                determined by our server once you chose to submit your order.
              </p>
            </div>
          </div>
        </div>
        <div class="field is-horizontal">
          <div class="field-label is-normal">
            <label class="label" for="location">Location</label>
          </div>
          <div class="field-body">
            <div class="field">
              <div class="control is-expanded has-icons-left">
                <input class="input" id="location" type="text" readonly :value="locationString">
                <span class="icon is-small is-left">
                <svg-icon type="mdi" :path="locationIcon">&nbsp;</svg-icon>
              </span>
              </div>
            </div>
          </div>
        </div>
        <div class="field is-horizontal">
          <div class="field-label is-normal">
            <label class="label" for="target">Lease To</label>
          </div>
          <div class="field-body">
            <div class="field">
              <div class="control is-expanded has-icons-left">
              <span class="select is-fullwidth" :class="{'is-danger': !leasingTarget, 'is-success': !!leasingTarget}">
                <select id="target" v-model="leasingTarget">
                  <option v-for="t in leasingTargets" :key="t" :value="t">{{ t }}</option>
                </select>
              </span>
                <span class="icon is-small is-left">
                <svg-icon type="mdi" :path="targetIcon">&nbsp;</svg-icon>
              </span>
              </div>
              <p class="help is-danger" v-if="!leasingTarget">Please select the account to lease the aircraft to</p>
            </div>
          </div>
        </div>
        <div class="field is-horizontal">
          <div class="field-label">
            <!-- Left empty for spacing -->
          </div>
          <div class="field-body">
            <div class="field">
              <div class="control">
                <label class="checkbox has-text-grey-dark">
                  <input type="checkbox" v-model="confirmToS">
                  I confirm I have read and agree to the
                  <router-link to="/terms" target="_blank">Terms of Service</router-link>.
                </label>
              </div>
            </div>
          </div>
        </div>
        <div class="field is-horizontal">
          <div class="field-label">
            <!-- Left empty for spacing -->
          </div>
          <div class="field-body">
            <div class="field">
              <div class="control">
                <button class="button is-primary" v-if="!!leasingTarget && confirmToS">Order Lease</button>
                <button class="button is-primary" disabled v-if="!leasingTarget || !confirmToS">Order Lease</button>
              </div>
            </div>
          </div>
        </div>
      </form>
    </base-modal>
  </section>
</template>

<script>
import SvgIcon from '@jamescoyle/vue-icon';
import BaseModal from "@/components/ui/BaseModal";
import BaseSpinner from "@/components/ui/BaseSpinner";
import {
  mdiAirplane,
  mdiCrosshairsGps,
  mdiCurrencyUsd,
  mdiCalendarStart,
  mdiCalendarEnd,
  mdiTargetAccount
} from '@mdi/js';

export default {
  name: "AircraftLeaseForm",
  components: {
    SvgIcon,
    BaseModal,
    BaseSpinner
  },
  computed: {
    aircraftIcon() {
      return mdiAirplane;
    },
    locationIcon() {
      return mdiCrosshairsGps;
    },
    rateIcon() {
      return mdiCurrencyUsd;
    },
    startIcon() {
      return mdiCalendarStart;
    },
    endIcon() {
      return mdiCalendarEnd;
    },
    targetIcon() {
      return mdiTargetAccount;
    },
    aircraft() {
      return this.$store.getters['aircraft/aircraft'].find(ac => ac.fse_id === this.id);
    },
    aircraftString() {
      return this.aircraft.reg + ' - ' + this.aircraft.ac_type + ' (FSE ID ' + this.aircraft.fse_id + ')';
    },
    locationString() {
      let ls = this.aircraft.location + ' ' + this.aircraft.location_details.name + ', ' + this.aircraft.location_details.city;
      if (this.aircraft.location_details.state !== "") {
        ls = ls + ', ' + this.aircraft.location_details.state;
      }
      ls = ls + ', ' + this.aircraft.location_details.country;
      return ls;
    },
    leasingTargets() {
      const username = this.$store.getters['auth/username'];
      const groups = this.$store.getters['auth/verifiedGroups'];
      const targets = groups.map(g => g.group);
      targets.sort();
      return [username, ...targets];
    },
    rateString() {
      return 'v$' + this.aircraft.rate.toLocaleString('en-US', {maximumFractionDigits: 0}) + ' per month';
    },
    startDate() {
      let d = new Date();
      return d.toISOString().split('T')[0] + ' (UTC)';
    },
    endDate() {
      let d = new Date();
      d.setUTCMonth(d.getUTCMonth() + 1);
      d.setUTCDate(d.getUTCDate() - 1);
      return d.toISOString().split('T')[0] + ' (UTC)';
    }
  },
  data() {
    return {
      isLoading: false,
      success: false,
      error: null,
      leasingTarget: null,
      confirmToS: false
    };
  },
  emits: ['close'],
  props: {
    show: {
      type: Boolean,
      required: true
    },
    id: {
      type: Number,
      required: true
    }
  },
  methods: {
    async processLeasingForm() {
      this.isLoading = true;
      const actionPayload = {
        aircraft: this.aircraft.fse_id,
        target: this.leasingTarget
      }
      console.log(actionPayload);
      try {
        await this.$store.dispatch('aircraft/submitLeaseRequest', actionPayload);
        await this.$store.dispatch('aircraft/loadAircraft', {forceRefresh: true});
        await this.$store.dispatch('contracts/loadContracts', {forceRefresh: true});
        this.success = true;
      } catch (error) {
        this.error = error.message || 'Failed to reserve aircraft for leasing, try later.';
      }
      this.isLoading = false;
    },
    handleError() {
      this.error = null;
    },
    handleSuccess() {
      this.success = false;
      this.$emit('close');
    }
  }
}
</script>

