<template>
  <div class="column is-12-mobile is-6-tablet is-3-desktop">
    <div class="card bm--card-equal-height">
      <div class="card-image">
        <figure class="image is-16by9">
          <img :src="img" :alt="reg">
        </figure>
      </div>
      <div class="card-content">
        <div class="media-content">
          <p class="subtitle is-6">{{ ac_class }}</p>
          <p class="title is-5">{{ model }}</p>
          <p class="subtitle is-6 mb-4 font-secondary">{{ leasingRate }} per month</p>
        </div>
        <div class="content is-size-7">
          <table class="table is-fullwidth is-hoverable is-narrow is-bordered">
            <tbody>
            <tr>
              <th>Registration:</th>
              <td>{{ reg }}</td>
            </tr>
            <tr>
              <th>Avionics:</th>
              <td>{{ avionics }}</td>
            </tr>
            <tr>
              <th>Engines:</th>
              <td>{{ engines }}</td>
            </tr>
            <tr>
              <th>Speed / Range:</th>
              <td>{{ speed }} kt TAS / {{ Math.round(range) }} NM</td>
            </tr>
            <tr>
              <th>Max Passengers</th>
              <td>{{ pax }} pax</td>
            </tr>
            <tr>
              <th>Max Payload</th>
              <td>{{ payload }} kg</td>
            </tr>
            <tr>
              <th>Airport:</th>
              <td>{{ location }} &ndash; {{ airport }}</td>
            </tr>
            <tr>
              <th>Location:</th>
              <td>{{ cityString }}</td>
            </tr>
            <tr>
              <th>Country:</th>
              <td>{{ country }}</td>
            </tr>
            <tr>
              <th>Region:</th>
              <td>{{ region }}</td>
            </tr>
            <tr>
              <th>Sub-Region:</th>
              <td>{{ subregion }}</td>
            </tr>
            </tbody>
          </table>
          <button @click="$emit('lease', id)" class="button is-primary is-pulled-right mt-4 is-outlined"
                  v-if="eligible">
            <span class="icon is-small">
              <svg-icon type="mdi" :path="cartIcon">&nbsp;</svg-icon>
            </span>
            <span>Lease</span>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SvgIcon from '@jamescoyle/vue-icon';
import {mdiCart} from '@mdi/js';

export default {
  name: "AircraftItem",
  emits: ['lease'],
  props: [
    'model',
    'img',
    'id',
    'rate',
    'reg',
    'ac_class',
    'home',
    'has_ifr',
    'has_ap',
    'has_gps',
    'location',
    'airport',
    'city',
    'state',
    'country',
    'region',
    'subregion',
    'engines',
    'range',
    'speed',
    'pax',
    'payload'
  ],
  components: {
    SvgIcon
  },
  computed: {
    leasingRate() {
      return 'v$' + this.rate.toLocaleString('en-US', {maximumFractionDigits: 0});
    },
    avionics() {
      let equipment = [];
      if (this.has_ifr) {
        equipment.push('IFR');
      }
      if (this.has_ap) {
        equipment.push('AP');
      }
      if (this.has_gps) {
        equipment.push('GPS');
      }
      if (equipment.length === 0) {
        equipment.push('VFR');
      }
      return equipment.join('/');
    },
    eligible() {
      return this.$store.getters['auth/isAuthenticated'] && this.$store.getters['auth/hasProfile'] && this.$store.getters['auth/isVerified'];
    },
    cartIcon() {
      return mdiCart;
    },
    cityString() {
      if (this.state === "") {
        return this.city
      }
      return this.city + ", " + this.state;
    }
  }
}
</script>

