<template>
  <div>
    <base-dialog :show="!!error" title="An error occurred!" @close="handleError">
      <p>{{ error }}</p>
    </base-dialog>
    <aircraft-lease-form v-if="show" :show="show" :id="id" @close="handleLeaseClose"></aircraft-lease-form>
    <section>
      <div v-if="isLoading">
        <base-spinner></base-spinner>
      </div>
      <div v-else-if="hasAircraft">
        <div class="card mt-4">
          <div class="card-content has-background-info-light">
            <div class="columns">

              <div class="column">
                <div class="field is-horizontal is-expanded">
                  <div class="field-label is-normal">
                    <label class="label" for="acclass">Category</label>
                  </div>
                  <div class="field-body">
                    <div class="field is-expanded">
                      <div class="control">
                        <div class="select is-fullwidth">
                          <select id="acclass" v-model="filterClass">
                            <option v-for="acc in aircraftClasses" :key="acc" :value="acc">{{ acc }}</option>
                          </select>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="column">
                <div class="field is-horizontal is-expanded">
                  <div class="field-label is-normal">
                    <label class="label" for="region">Region</label>
                  </div>
                  <div class="field-body">
                    <div class="field is-expanded">
                      <div class="control">
                        <div class="select is-fullwidth">
                          <select id="region" v-model="filterRegion" @change="updateRegion">
                            <option v-for="region in regions" :key="region" :value="region">{{ region }}</option>
                          </select>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="column">
                <div class="field is-horizontal">
                  <div class="field-label is-normal">
                    <label class="label" for="subregion">Subregion</label>
                  </div>
                  <div class="field-body">
                    <div class="field is-expanded">
                      <div class="control">
                        <div class="select is-fullwidth">
                          <select id="subregion" v-model="filterSubRegion">
                            <option v-for="region in subRegions" :key="region" :value="region">{{ region }}</option>
                          </select>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="columns is-multiline mb-4">
          <aircraft-item v-for="ac in filteredAircraft"
                         :key="ac.fse_id"
                         :model="ac.ac_type"
                         :img="ac.image"
                         :id="ac.fse_id"
                         :rate="ac.rate"
                         :reg="ac.reg"
                         :ac_class="ac.ac_class"
                         :home="ac.home"
                         :has_ap="ac.has_ap"
                         :has_gps="ac.has_gps"
                         :has_ifr="ac.has_ifr"
                         :location="ac.location"
                         :airport="ac.location_details.name"
                         :city="ac.location_details.city"
                         :state="ac.location_details.state"
                         :country="ac.location_details.country"
                         :region="ac.location_details.region"
                         :subregion="ac.location_details.subregion"
                         :engines="ac.engines"
                         :range="ac.range"
                         :speed="ac.speed"
                         :pax="ac.passengers"
                         :payload="ac.payload"
                         @lease="doLease"
          >
          </aircraft-item>
        </div>
      </div>
      <h3 v-else>No aircraft data found.</h3>
    </section>
  </div>
</template>

<script>
import BaseDialog from "@/components/ui/BaseDialog";
import BaseSpinner from "@/components/ui/BaseSpinner";
import AircraftItem from "@/components/aircraft/AircraftItem";
import AircraftLeaseForm from "@/components/aircraft/AircraftLeaseForm";

function compareAircraft(a, b) {
  if (a.rate < b.rate) {
    return -1;
  }
  if (a.rate > b.rate) {
    return 1;
  }
  return 0;
}

export default {
  name: "AircraftList",
  components: {
    BaseDialog,
    BaseSpinner,
    AircraftItem,
    AircraftLeaseForm
  },
  data() {
    return {
      isLoading: false,
      error: null,
      id: null,
      show: false,
      filterClass: 'any',
      filterRegion: 'any',
      filterSubRegion: 'any',
      filterPrevRegion: 'any',
      filterPrevSubRegion: 'any'
    }
  },
  computed: {
    aircraft() {
      return this.$store.getters['aircraft/aircraft'];
    },
    hasAircraft() {
      return !this.isLoading && this.$store.getters['aircraft/hasAircraft'];
    },
    sortedAircraft() {
      let sortedAC = [...this.aircraft];
      sortedAC.sort(compareAircraft);
      return sortedAC;
    },
    filteredAircraft() {
      let ac = this.sortedAircraft;
      if (this.filterClass !== 'any') {
        ac = ac.filter(x => x.ac_class === this.filterClass);
      }
      if (this.filterRegion !== 'any') {
        ac = ac.filter(x => x.location_details.region === this.filterRegion);
      }
      if (this.filterSubRegion !== 'any') {
        ac = ac.filter(x => x.location_details.subregion === this.filterSubRegion);
      }
      return ac;
    },
    regions() {
      const regs = [...new Set(this.sortedAircraft.map(x => x.location_details.region))];
      return ['any', ...regs];
    },
    subRegions() {
      let ac = this.sortedAircraft;
      if (this.filterRegion !== 'any') {
        ac = ac.filter(x => x.location_details.region === this.filterRegion);
      }
      const sub_regs = [...new Set(ac.map(x => x.location_details.subregion))];
      return ['any', ...sub_regs];
    },
    aircraftClasses() {
      const acc = [...new Set(this.sortedAircraft.map(x => x.ac_class))];
      return ['any', ...acc];
    }
  },
  methods: {
    async loadAircraft(force = false) {
      this.isLoading = true;
      try {
        await this.$store.dispatch('aircraft/loadAircraft', {forceRefresh: force});
      } catch (error) {
        this.error = error.message || 'Could not load aircraft data';
      }
      this.isLoading = false;
    },
    handleError() {
      this.error = null;
    },
    doLease(id) {
      this.id = id;
      this.show = true;
    },
    handleLeaseClose() {
      this.id = null;
      this.show = false;
    },
    updateRegion() {
      if (this.filterRegion !== this.filterPrevRegion) {
        this.filterSubRegion = 'any';
        this.filterPrevSubRegion = 'any';
        this.filterPrevRegion = this.filterRegion;
      }
    },
    updateSubRegion() {
      if (this.filterSubRegion !== this.filterPrevSubRegion) {
        this.filterRegion = 'any';
        this.filterPrevRegion = 'any';
        this.filterPrevSubRegion = this.filterSubRegion;
      }
    }
  },
  created() {
    this.loadAircraft(false);
  }
}
</script>

<style scoped>
ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
</style>